import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';

import { UsuariosProvider } from '../../providers/usuarios/usuarios';

import { ChatPage } from '../chat/chat';
import { MensajesPage } from '../mensajes/mensajes';

import { AlertController } from 'ionic-angular';

import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  title: string = "";
  message: string = "";
  items: Observable<any[]>;
  datos: string = "";

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public _usuario: UsuariosProvider,
    private loadingCtrl: LoadingController,
    private toastCtrl: ToastController,
    public alertCtrl: AlertController,
    public afDB: AngularFireDatabase) {

    this._usuario.obtener_storage().then(usr => {

      this.items = afDB.list("db_users/usuarios/" + usr + "/sintomas").valueChanges();

    });

   

  }

  logchtas() {

    let loading = this.loadingCtrl.create({

      content: "Espere por favor..."

    });

    loading.present().then(() => {

      this._usuario.comproTipo().then(_data => {

        if (_data == "usuario") {

          this.navCtrl.push(ChatPage);

        } else {

          this.navCtrl.push(MensajesPage);

        }

      }).catch(error => {

        loading.dismiss();

        this.toastCtrl.create({

          message: 'ERROR de carga de mensajes',
          duration: 1500
        }).present();

        console.log("ERROR en logIn: " + JSON.stringify(error));

      });

      loading.dismiss();
    });

  }

  frcuencia(_data) {

    this.title = "nueva " + _data;
    this.message = "ingrese nueva  " + _data;

    let prompt = this.alertCtrl.create({
      title: this.title,
      message: this.message,
      inputs: [
        {
          name: 'title',
          placeholder: 'Title'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            //console.log('Cancel clicked');
          }
        },
        {
          text: 'Save',
          handler: data => {
            this.datos = data.title;

            this._usuario.obtener_storage().then(usr => {

              this.afDB.list("db_users/usuarios/" + usr + "/sintomas").push(
                _data + ":" + this.datos
              );

            });

          }
        }
      ]
    });

    prompt.present();
  }

  logout() {
    this._usuario.borrar_storage().then(() => {

      window.location.reload(false);

    })
  }


}



