import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { LoginPage } from '../pages/login/login';
import { HomePage } from '../pages/home/home';
import { AboutPage } from '../pages/about/about';
import { MensajesPage } from '../pages/mensajes/mensajes';
import { ChatPage } from '../pages/chat/chat';
import { SlidesPage } from '../pages/slides/slides';
import { ChatmePage } from '../pages/chatme/chatme';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule, AngularFireDatabase } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';

import { IonicStorageModule } from '@ionic/storage';

import { UsuariosProvider } from '../providers/usuarios/usuarios';

export const firebaseConfig = {
  apiKey: "AIzaSyC9bEftmOEEy4T6Ew14Wm1jFvfkSap4mqw",
  authDomain: "medicblake-75c99.firebaseapp.com",
  databaseURL: "https://medicblake-75c99.firebaseio.com",
  projectId: "medicblake-75c99",
  storageBucket: "medicblake-75c99.appspot.com",
  messagingSenderId: "792046188013"
};

@NgModule({
  declarations: [
    MyApp,
    LoginPage,
    HomePage,
    AboutPage,
    MensajesPage,
    ChatPage,
    SlidesPage,
    ChatmePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    HomePage,
    AboutPage,
    MensajesPage,
    ChatPage,
    SlidesPage,
    ChatmePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AngularFireDatabase,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    UsuariosProvider
  ]
})
export class AppModule {}



