import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { UsuariosProvider } from '../../providers/usuarios/usuarios';


@IonicPage()
@Component({
  selector: 'page-chat',
  templateUrl: 'chat.html',
})
export class ChatPage {

  items: Observable<any[]>;
  mensaje: string = "";

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public _usuario: UsuariosProvider,
    private loadingCtrl: LoadingController,
    private toastCtrl: ToastController,
    private afDb: AngularFireDatabase) {

    let loading = this.loadingCtrl.create({

      content: "Espere por favor..."

    });

    loading.present().then(() => {
 
      this._usuario.obtener_storage().then(_data => {

        this.afDb.object('/db_users/usuarios/' + _data).valueChanges().subscribe(result => {
           
          this.items = this.afDb.list('/db_users/usuarios/'+result.doctor+'/mensajes/' + _data).valueChanges();

        });

      }).catch(error => {

        loading.dismiss();

        this.toastCtrl.create({

          message: 'ERROR de carga mensajes',
          duration: 1500
        }).present();

        console.log("ERROR en logIn: " + JSON.stringify(error));

      });

      loading.dismiss();
    });

  }

  enviar() {

    let loading = this.loadingCtrl.create({

      content: "Espere por favor..."

    });

    loading.present().then(() => {

      this._usuario.obtener_storage().then(_data => {

        this.afDb.object('/db_users/usuarios/snow/mensajes/usuarios/' + _data).update( {        
          "usr": _data
        });

        this.afDb.list('/db_users/usuarios/snow/mensajes/' + _data).push({ 
          mensaje: this.mensaje,
          usuario: _data
        });

        this.mensaje= "";

      }).catch(error => {

        loading.dismiss();

        this.toastCtrl.create({

          message: 'ERROR de envio mensajes',
          duration: 1500
        }).present();

        console.log("ERROR en logIn: " + JSON.stringify(error));

      });

      loading.dismiss(); 
    });

    

  }

}



