import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';

import { UsuariosProvider } from '../../providers/usuarios/usuarios';
import { HomePage } from '../home/home';
import { SlidesPage } from '../slides/slides';


@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  user: string = "";
  password: string = "";

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public _usuario: UsuariosProvider,
    private loadingCtrl: LoadingController,
    private toastCtrl: ToastController) {
  }

  logIn() {

    let loading = this.loadingCtrl.create({

      content: "Espere por favor..."

    });

    loading.present();

    this._usuario.logIn().then(_dataUsuario => {

      if (_dataUsuario == true) {
        this.navCtrl.push(SlidesPage);
      } else {
        this.navCtrl.push(HomePage);
      }

      loading.dismiss();

    }).catch(error => {

      loading.dismiss();

      this.toastCtrl.create({

        message: 'ERROR de inicio se sesión',
        duration: 1500
      }).present();

      console.log("ERROR en logIn: " + JSON.stringify(error));

    });
  }

  logInUser() {

    let loading = this.loadingCtrl.create({

      content: "Espere por favor..."

    });

    loading.present();

    this._usuario.logInUsr(this.user, this.password).then(_dataUsuario => {

      if (_dataUsuario == true) {
        this.navCtrl.push(HomePage);
      } else {
        this.toastCtrl.create({

          message: 'Usuario y/o contraseña incorrectos',
          duration: 1500
        }).present();
      }

      loading.dismiss();

    }).catch(error => {

      loading.dismiss();

      this.toastCtrl.create({

        message: 'ERROR de inicio se sesión',
        duration: 1500
      }).present();

      console.log("ERROR en logIn: " + JSON.stringify(error));

    });


  }

}


