import { Injectable } from '@angular/core';

import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase } from 'angularfire2/database';
import * as firebase from 'firebase/app';

import { Observable } from 'rxjs/Observable';

import { Storage } from '@ionic/storage';
import { Platform } from 'ionic-angular';

@Injectable()
export class UsuariosProvider {

  items: Observable<any[]>;

  constructor(
    private afAuth: AngularFireAuth,
    private afDb: AngularFireDatabase,
    private storage: Storage,
    private plt: Platform
  ) {

  }

  logIn() {

    let promesa = new Promise((resolve, reject) => {

      this.afAuth.auth.signInWithPopup(new firebase.auth.FacebookAuthProvider()).then(result => {

        this.afDb.object("/db_users/" + result.additionalUserInfo.profile.id).update(result.additionalUserInfo.profile);

        //this.guardar_storage(result.additionalUserInfo.profile.id, "user");

        resolve(result.additionalUserInfo.isNewUser);

      });
    }).catch(error => console.log("Error en promesa Service: " + JSON.stringify(error)));

    return promesa;
  }

  logInUsr(user, password) {

    let promesa = new Promise((resolve, reject) => {

      this.afDb.object("/db_users/usuarios/" + user).valueChanges().subscribe(result => {

        if (result.password == password) {

          this.guardar_storage(result.nik, "user");
          resolve(true);

        } else {
          resolve(false);
        }

      });
    }).catch(error => console.log("Error en promesa Service: " + JSON.stringify(error)));

    return promesa;
 
  }

  veriUsr(usuario, pass, tip) {
    let promesa = new Promise((resolve, reject) => {

      this.afDb.object("/db_users/usuarios/" + usuario).valueChanges().subscribe(result => {

        if (result) {
          resolve(false);
        } else {
          this.obtener_storage().then(usr => {

            this.afDb.object("/db_users/usuarios/" + usuario).update({ id: usr, nik: usuario, password: pass, tipo: tip });
            this.guardar_storage(usuario, "user");
            resolve(true);

          });

        }

      });
    }).catch(error => console.log("Error en promesa Service: " + JSON.stringify(error)));

    return promesa;
  }

  veriMedi(usuariod, usuario) { 

    let promesa = new Promise((resolve, reject) => {

      this.afDb.object("/db_users/usuarios/" + usuariod).valueChanges().subscribe(result => {

        if (result == null) { 

          resolve("false");

        } else if (result.tipo == "doctor") { 

          this.afDb.object("/db_users/usuarios/" + usuario).update({ doctor: usuariod });
          this.afDb.object("/db_users/usuarios/" + usuariod + "/mensajes/" + usuario).update({ menssage: "hola" });
          resolve(result.tipo);
        } else {
          resolve(result.tipo);
        }



      });
    }).catch(error => console.log("Error en promesa Service: " + JSON.stringify(error)));

    return promesa;

  }

  comproTipo() {

    let promesa = new Promise((resolve, reject) => {

      this.obtener_storage().then(usr => {

        this.afDb.object("/db_users/usuarios/" + usr).valueChanges().subscribe(result => {

          resolve(result.tipo);

        });

      });

      
    }).catch(error => console.log("Error en promesa Service: " + JSON.stringify(error)));

    return promesa;

  }

  guardar_storage(id, op) {
    let promesa = new Promise((resolve, reject) => {
      if (this.plt.is("cordova")) {
        this.storage.set(op, id);
      } else {
        localStorage.setItem(op, id);
      }
      resolve();
    }).catch(error => console.log("Error en promesa Service: " + JSON.stringify(error)));
    return promesa;
  }

  obtener_storage() { 
    let promesa = new Promise((resolve, reject) => {
      if (this.plt.is("cordova")) {
        this.storage.ready().then(() => {
          this.storage.get("user").then(user => {
            resolve(user);
          })
        })
      } else {
        resolve(localStorage.getItem("user"));
      }
    }).catch(error => console.log("Error en promesa Service: " + JSON.stringify(error)));
    return promesa;
  }

  borrar_storage() {
    let promesa = new Promise((resolve, reject) => {
      if (this.plt.is("cordova")) {
        this.storage.remove("user");
      } else {
        localStorage.clear();
      }
      resolve();
    }).catch(error => console.log("Error en promesa Service: " + JSON.stringify(error)));
    return promesa;

  }
}



