import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';


import { ViewChild, AfterViewInit } from '@angular/core';
import { Slides } from 'ionic-angular';

import { UsuariosProvider } from '../../providers/usuarios/usuarios';

import { HomePage } from '../home/home';


@IonicPage()
@Component({
  selector: 'page-slides',
  templateUrl: 'slides.html',
})
export class SlidesPage implements AfterViewInit {

  gender: string = "";
  usuario: string = "";
  usuariod: string = "";
  password: string = "";
  password2: string = "";

  @ViewChild(Slides) slides: Slides;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public _usuario: UsuariosProvider,
    private loadingCtrl: LoadingController,
    private toastCtrl: ToastController) {
  }

  ionViewDidLoad() {

  }

  ngAfterViewInit() {

    this.slides.lockSwipes(true);
    this.slides.freeMode = false;
    this.slides.paginationType = "progress";

  }

  continuar() {

    let loading = this.loadingCtrl.create({

      content: "Espere por favor..."

    });

    loading.present().then(() => {
      if (this.password == this.password2) {

        if (this.password[7] && this.gender != "") {

          this._usuario.veriUsr(this.usuario, this.password, this.gender).then(_dataUsuario => {

            if (_dataUsuario == true) {

              if (this.gender == "doctor") {

                this.navCtrl.push(HomePage);

              } else {

                this.slides.lockSwipes(false);
                this.slides.slideNext();
                this.slides.lockSwipes(true);

              }

            } else {

              this.toastCtrl.create({

                message: 'El usuario ya existe',
                duration: 1500
              }).present();

            }

          }).catch(error => {

            this.toastCtrl.create({

              message: 'ERROR',
              duration: 1500
            }).present();

            console.log("ERROR " + JSON.stringify(error));

          });

        } else {
          this.toastCtrl.create({
            message: 'La contraseña debe tener minimo 8 caracteres y se debe seleccionar un tipo de cuenta',
            duration: 1500
          }).present();

        }

      } else {
        this.toastCtrl.create({
          message: 'Las contraseñas no coinciden',
          duration: 1500
        }).present();

      }

      loading.dismiss();

    });

  }

  validarMedico() {

    let loading = this.loadingCtrl.create({

      content: "Espere por favor..."

    });

    loading.present().then(() => {

      this._usuario.veriMedi(this.usuariod, this.usuario).then(_data =>{

        if(_data == "doctor"){

          this.navCtrl.push(HomePage);

        }else{

          this.toastCtrl.create({
            message: this.usuariod+ ' no tiene rol de doctor',
            duration: 1500
          }).present();

        }
        
      });

      loading.dismiss();
    });

  }

}



